import { NavLink } from "react-router-dom";
import './NavBar.css';
const NavBar = () => {
  const navLinkGen = (to, text) => <NavLink to={ to } className='NavBar-link'>{ text }</NavLink>;
  return (
    <ul className='NavBar-display'>
      <li>
        { navLinkGen('/', 'Home') }
      </li>
      <li>
        { navLinkGen('/about', 'About') }
      </li>
    </ul>
  );
};

export default NavBar;
